<template>
  <div>
    <h3> <button style="width: 150px" @click="getAllUserList">User refresh</button></h3>
    <h4>(    )</h4>

    <table>
      <thead>
      <tr>
        <th> </th>
        <th> </th>
        <th v-if="roundList.length != 0"> </th>
        <th> </th><th>Battle Type</th>
        <th><label></label></th>
        <th><label></label></th>
        <th> URL()</th>
        <th></th>
      </tr>
      </thead>
      <tbody>
      <tr>
        <td><datepicker v-model="recordData.date" :language="languages[1]" :format="dateFormat"></datepicker></td>
        <td>
          <div class="user-container">
            <vue-multiselect v-model="recordData.battleId" placeholder=" " label="name" track-by="name" selectLabel="" selectedLabel=" " deselectLabel="" :options="battleList" :multiple="false"></vue-multiselect>
          </div>
        </td>
        <td v-if="roundList.length != 0">
          <select v-model="recordData.roundNum">
            <option v-for="num in roundList" :value="num">{{num}}</option>
          </select>
        </td>
        <td>
          <div class="user-container">
            <vue-multiselect v-model="recordData.map" placeholder="  " label="name" track-by="name" selectLabel="" selectedLabel=" " deselectLabel="" :options="mapList" :multiple="false"></vue-multiselect>
          </div>
        </td>
        <td>
          <div>
            <select v-model="recordData.battleType">
              <option v-for="type in $defs.gameTypeList" :value="type.type">{{type.name}}</option>
            </select>
          </div>
        </td>
        <td>
          <div class="user-container">
            <vue-multiselect v-model="recordData.winners" placeholder=" " track-by="userName" selectLabel="" selectedLabel=" " deselectLabel=""
                             :options="userList" :multiple="true" :taggable="true" @select="addWinners" :custom-label="userSearchLabel" :show-labels="false"></vue-multiselect>
          </div>
        </td>
        <td>
          <div class="user-container">
            <vue-multiselect v-model="recordData.losers" placeholder=" " label="userName" track-by="userName" selectLabel="" selectedLabel=" " deselectLabel=""
                             :options="userList" :multiple="true" :taggable="true" @select="addLosers" :custom-label="userSearchLabel" :show-labels="false"></vue-multiselect>
          </div>
        </td>
        <td>
          <div>
            <input type="url" v-model="recordData.videoLink" placeholder="  url ">
          </div>
        </td>
        <td>
          <div>
            <button @click="addRecord"></button>
          </div>
        </td>
      </tr>
      </tbody>
    </table>

    <hr>

    <h3> </h3>
    <h4>    .</h4>
    <div>
      <input type="text" v-model="mapName" placeholder=" "><button class="btn-record-page" @click="addMap">add map</button>
    </div>

    <hr>

    <div>
      <h3>   (   20)<button class="btn-record-page" @click="getAllRecords">refresh</button></h3>
      <h4>(Refresh        !)</h4>
      <label>
        (<label> : </label>
        <label class="terran"></label>
        <label class="zerg"></label>
        <label class="protoss"></label>
        <label class="random"></label>
        )
      </label>
      <table>
        <thead>
        <tr>
          <th> </th>
          <th></th><th></th><th>Battle Type</th>
          <th><label></label></th>
          <th><label></label></th>
          <th> URL</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="record in recordList">
          <td>{{record.date | moment(mDateFormat)}}</td>
          <td>{{record.battleId.name}}</td>
          <td>{{record.map.name}}</td>
          <td>{{$defs.gameTypeList[record.battleType].name}}</td>
          <td><label v-for="user in record.winners" class="user-label" :class="user.tribe">{{user.userName}}</label></td>
          <td><label v-for="user in record.losers" class="user-label" :class="user.tribe">{{user.userName}}</label></td>
          <td>{{record.videoLink}}</td>
        </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import Datepicker from 'vuejs-datepicker'
  import {en, ko} from 'vuejs-datepicker/dist/locale'
  export default {
    name: "CreateRecord",
    components: {Datepicker},
    data () {
      return {
        languages: [en, ko],
        dateFormat: 'yyyy-MM-dd',
        mDateFormat: 'YYYY-MM-DD',
        mapName: '',
        battleList: [],
        userList: [],
        mapList: [],
        recordList: [],
        recordData: { battleId: '', battleType: 0, roundNum: 0, winners: [], losers: [], date: new Date(), map: '', videoLink: '', writer: '' },
        //battleType: '',
      }
    },
    methods: {
      getMapList () {
        this.$lcordAPI.map.getAllMaps()
          .then((resp) => {
            this.mapList = resp
          })
      },
      getAllUserList () {
        this.$lcordAPI.user.getAllUsers()
          .then((resp) => {
            this.userList = resp
          })
      },
      getBattleList () {
        this.$lcordAPI.battle.getListByProgress(true) //   
          .then((resp) => {
            this.battleList = resp
          })
      },
      getAllRecords () {
        this.recordList = []
        this.$lcordAPI.record.getAllRecords()
          .then((resp) => {
            this.recordList = resp
          })
      },

      addMap () {
        if (this.mapName != '') {
          this.$lcordAPI.map.create(this.mapName)
            .then(() => {
              this.getMapList()
              alert('  !')
            })
        } else {
          alert('   . -_-')
        }
      },

      userSearchLabel ({userName, bNetId, tribe}) {
        //return `${userName}(${bNetId.split(' ')[0]})`
        return `${userName}(${bNetId})`
      },

      addWinners () {
        if (this.recordData.winners.length > this.recordData.battleType) {
          this.recordData.winners.splice(0, 1)
        }
      },
      addLosers () {
        if (this.recordData.losers.length > this.recordData.battleType) {
          this.recordData.losers.splice(0, 1)
        }
      },

      addRecord() {
        if (this.recordData.battleId == '' || this.recordData.winners.length == 0 || this.recordData.losers.length == 0 || this.recordData.map == '') {
          alert('   .')
        } else {
          if (this.userDBIndex == '') {
            alert(' .')
            this.$router.push({name: 'Login'})
          } else {
            this.recordData.writer = this.userDBIndex
            this.$lcordAPI.record.create(this.recordData)
              .then((resp) => {
                if (resp.isAxiosError) {
                  alert('   .')
                } else {
                  this.recordData.map = ''
                  this.recordData.battleType = 0
                  this.recordData.winners = []
                  this.recordData.losers = []
                  this.getAllRecords()
                }
              })
          }
        }
      },
    },
    mounted() {
      this.getMapList()
      this.getAllUserList()
      this.getBattleList()
      this.getAllRecords()

      this.recordData.battleType = 0
    },
    computed: {
      roundList: function () {
        let retArray = []
        if (this.recordData.battleId != null) {
          if (this.recordData.battleId.isRound) {
            for (let i = 0; i < this.recordData.battleId.roundCount; i++) {
              retArray.push(i + 1)
            }
          }
        }
        return retArray
      },
      ...mapGetters({
        userDBIndex: 'getUserDBIndex',
      })
    }
  }
</script>

<style scoped>
  /*.user-container {
    width: 200px;
    margin: auto;
  }

  .user-label {
    margin: 5px;
  }

  .btn-record-page { max-width: 150px; padding: 6px }*/
</style>



