<template>
  <div class="container-account">
    <div class="div-account-detail about">
      <h2>LCORD?</h2>
      <ul>
        <li>LCORD(LG Craft RecorD) ( LG  )       .</li>
        <li>      (ex:  >>> )    .</li>
        <br>
        <div style="font-weight: bold;">* *</div>
        <li><b> :</b>     .      .     ,        .</li>
        <li><b> :</b>    /  .   /    .</li>
        <li>        .     .^^</li>
        <li><b> URL :</b>    ,      Web .</li>
        <li><b>    ,    .</b></li>
      </ul>
    </div>
    <br>
    <div class="container-info">
      <div class="div-account-detail info">
        <h2>  </h2>
        <div class="div-detail">
          <h3> </h3>
          <div style="display: flex; flex-direction: row; justify-content: center; align-items: center">
            <input type="text" v-model="accountParams.userId" placeholder="(/ )" style="width: 45%">
            <button class="btn-dup-check" @click="checkDupUserId"></button>
          </div>
          <div>
            <input type="password" v-model="accountParams.pwd" required placeholder="">
          </div>
          <div>
            <input type="password" v-model="accountParams.pwd_confirm" required placeholder=" ">
          </div>
          <h3> <i class="far fa-question-circle" title="LCORD        ,     ."></i></h3>
          <div>
            <input type="text" v-model="accountParams.userName" required placeholder="">
          </div>
          <div>
            <input type="text" v-model="accountParams.bNetId" required placeholder=" ">
          </div>
          <div>
            <input type="text" v-model="accountParams.afreecaId" required placeholder=" TV ">
          </div>
          <div>
            <input type="text" v-model="accountParams.nickName" required placeholder="">
          </div>
          <div style="margin-bottom: 5px">
            <vue-multiselect v-model="accountParams.tribe" placeholder=" " :options="$defs.tribes" label="name" track-by="name" selectLabel="" selectedLabel=" " deselectLabel=""></vue-multiselect>
          </div>
          <div style="margin-bottom: 5px">
            <vue-multiselect v-model="accountParams.tier" placeholder=" " :options="$defs.tierList" label="name" track-by="name" selectLabel="" selectedLabel=" " deselectLabel=""></vue-multiselect>
          </div>
        </div>
      </div>

      <br>

      <div class="div-account-detail info">
        <h2>  </h2>
        <div class="div-detail">
          <input type="number" v-model="optionalInfo.apm" placeholder=" APM()">
          <div style="margin-bottom: 5px">
            <vue-multiselect v-model="optionalInfo.grade" placeholder=" ( ) " :options="$defs.grades" label="name" track-by="name" selectLabel="" selectedLabel=" " deselectLabel=""></vue-multiselect>
          </div>
          <textarea v-model="optionalInfo.comment" placeholder="~ ~  !"></textarea>
        </div>
      </div>
      <br>
      <button @click="createAccount" style="width: 100%"> </button>
    </div>
    <div class="error-text">{{error}}</div>
  </div>
</template>

<script>
export default {
  name: 'CreateAccount',
  data () {
    return {
      accountParams: {
        userId: '',
        pwd: '',
        pwd_confirm: '',
        userName: '',
        bNetId: '',
        nickName: '',
        afreecaId: '',
        tier: '',
        tribe: '',
        optionalInfo: {}
      },
      optionalInfo: { apm: '', grade: '', comment: '' },
    }
  },
  methods: {
    async createAccount () {
      this.accountParams.optionalInfo = this.optionalInfo
      this.$lcordAPI.user.create(this.accountParams)
        .then((resp) => {
          if (resp.isAxiosError) {
            alert(' ')
          } else {
            this.$router.push({name: 'Login'})
          }
        })
    },
    async checkDupUserId () {
      if (this.accountParams.userId != '') {
        this.$lcordAPI.user.checkId(this.accountParams)
          .then(resp => {
            if (resp.isAxiosError) {
              alert('   .')
            } else {
              alert('  .')
            }
          })
      } else {
        alert('   .')
      }
    }
  },
  beforeMount() {

  }
}
</script>

<style scoped>
  li { text-align: left; padding-right: 15px; }
  .error-text {
    color: red;
    min-height: 30px;
  }

  textarea {
    width: 98%;
    margin-bottom: 5px;
  }
</style>



